/*STYLE BUTTONS*/
.btn{
  margin-left: 0;
  padding: 0;
  width: 100%;
  a{
    border-bottom: none;
  }
  button{
    border: none;
    cursor: pointer;
    font-size: 16px;
    color: #fff;
    max-width: 180px;
    height: 40px;
    text-transform: none;
    font-weight: normal;
    box-sizing: border-box;
    border-radius: 21px;
    width: 100%;
    background: $btn-color;
    padding: 0 20px;
    font-weight: 400;
    @include trans(0.2);
    outline: none;
    &:hover{
         background: #b22126;
         color: #fff;

    }
    &:active {
        background: #b22126;
        color: #fff;
    }
  }
}
.btn-transparent{
  margin-left: 0;
  padding: 0;
  width: 100%;
  a{
    border-bottom: none;
  }
  button{
    border: 2px solid $btn-color;
    cursor: pointer;
    color: $btn-color;
    max-width: 180px;
    height: 40px;
    text-transform: none;
    font-weight: normal;
    box-sizing: border-box;
    border-radius: 20px;
    width: 100%;
    padding: 0 20px;
    font-size: 16px;
    font-weight: 500;
    background: none;
    @include trans(0.3);
    outline: none;
    &:hover{
         background: $btn-color;
         color: #fff;

    }
    &:active {
        background: #b22126;
        color: #fff;
    }
  }
}

