    column-width: 250px;
    margin: 0;
    padding: 0;
    list-style: none;
    counter-reset: number;
}




/*//////////////////PRELOADER*/
.loaderArea{
    position: fixed;
    z-index: 999;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0,0,0,0.96);
}
.load{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);
  /*change these sizes to fit into your project*/
  width:100px;
  height:100px;
}
.load hr{border:0;margin:0;width:40%;height:40%;position:absolute;border-radius:50%;animation:spin 2s ease infinite}

.load :first-child{background:#fff;animation-delay:-1.5s}
.load :nth-child(2){background:$accent-color;animation-delay:-1s}
.load :nth-child(3){background:#fff;animation-delay:-0.5s}
.load :last-child{background:$accent-color}

@keyframes spin{
  0%,100%{transform:translate(0)}
  25%{transform:translate(160%)}
  50%{transform:translate(160%, 160%)}
  75%{transform:translate(0, 160%)}
}

